import $ from 'jquery'

var modalTpl = `
<div class="xw-modal">
    <div class="overlay"></div>
    <div class="wrapper"></div>
</div>
`
/**
 * ,bodymodal,
 */
export default class Modal {
    /**
     * modal,
     * @param content - modalhtml
     * @param id - content, modal,
     */
    constructor(content, id) {
        this._modal = $(modalTpl)
        this.id = id

        this._modal.on('click', '.overlay, .close', () => this.close())

        content && this._modal.find('.wrapper').html(content)
        $(document.body).append(this._modal)
        this.close()
    }
    close() {
        this._modal.hide()
    }
    open() {
        this._modal.show()
    }
    replace(content, id) {
        this._modal.find('.wrapper').html(content)
        this._modal.id = id
        return this
    }
    on() {
        this._modal.on(...arguments)
    }
    destroy() {
        $(document.body).remove(this._modal)
    }
}


